:root {
    --links: 231.8, 40.8%, 24.5%;

    --light: 60, 100%, 73%;
    --slight: 98, 100%, 100%;
    --text: 0, 0%, 100%;
    --accent: 42, 95%, 51%;
    --saccent: 108, 57%, 79%;
    --dark: 41, 100%, 38%;
    --compliment: 162, 95%, 51%;

    --green-h: 112.2;
    --green-s: 37.1%;
    --green-l: 48.6%;
    --green: var(--green-h), var(--green-s), var(--green-l);

    --red-h: 4;
    --red-s: 74.9%;
    --red-l: 46.9%;
    --red: var(--red-h), var(--red-s), var(--red-l);

    --black: 0, 0%, 0%;

    --sdark-h: 110;
    --sdark-s: 29%;
    --sdark-l: 40%;
    --sdark: var(--sdark-h), var(--sdark-s), var(--sdark-l);

    --background-h: 42.8;
    --background-s: 100%;
    --background-l: 56.9%;
    --background: var(--background-h), var(--background-s), var(--background-l);
}

/* CSS reset */

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section {
  display: block;
}

nav ul {
  list-style :none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input, select {
  vertical-align :middle;
}
/* END CSS RESET */


.center {
  text-align: center;
  margin-inline: auto;
}

.unselectable {
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}

.hidden {
    display: none !important;
}

.rangeSlider {
    position: relative;
    margin-top: 2ch;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: flex-start;
    width: 30vw;
}
.slider-track{
    width: 100%;
    height: 5px;
    position: relative;
    margin: auto;
    margin-block: 15px;
    border-radius: 5px;
}
.slider-question{
    width: 15ch;
    margin-inline: auto;
    /*text-align: center;*/
    padding: 0.5ch;
}
.values{
    background-color: #3264fe;
    width: 32%;
    position: relative;
    margin: auto;
    padding: 10px 0;
    border-radius: 5px;
    text-align: center;
    font-weight: 500;
    font-size: 25px;
    color: #ffffff;
}
.values:before{
    content: "";
    position: absolute;
    height: 0;
    width: 0;
    border-top: 15px solid #3264fe;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    margin: auto;
    bottom: -14px;
    left: 0;
    right: 0;
}

input[type="range"]{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 100%;
    outline: none;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    background-color: transparent;
    pointer-events: none;
}

input[type="range"]::-webkit-slider-runnable-track{
    -webkit-appearance: none;
    height: 5px;
}
input[type="range"]::-moz-range-track{
    -moz-appearance: none;
    height: 5px;
}
input[type="range"]::-ms-track{
    appearance: none;
    height: 5px;
}
input[type="range"]::-webkit-slider-thumb{
    -webkit-appearance: none;
    height: 1.7em;
    width: 1.7em;
    background-color: #3264fe;
    cursor: pointer;
    margin-top: -9px;
    pointer-events: auto;
    border-radius: 50%;
    border: 2px solid #3264fe;
}
input[type="range"]::-moz-range-thumb{
    -webkit-appearance: none;
    height: 1.7em;
    width: 1.7em;
    cursor: pointer;
    border-radius: 50%;
    background-color: #3264fe;
    pointer-events: auto;
    border: 2px solid #3264fe;
}
input[type="range"]::-ms-thumb{
    appearance: none;
    height: 1.7em;
    width: 1.7em;
    cursor: pointer;
    border-radius: 50%;
    background-color: #3264fe;
    pointer-events: auto;
    border: 2px solid #3264fe;
}
input[type="range"]:active::-webkit-slider-thumb{
    background-color: #ffffff;
    border: 3px solid #3264fe;
}

