.logo {
  min-width: 50px;
  width: 4vw;
  display: compact;
  justify-content:center; /* horizontally center */
  align-items:center;    /* vertically center */
  position: absolute;
  background:transparent;
  content:url("./components/data/images/logo_text.png");
  left: 0%;
  bottom: 0%;
}

.fancyButton {
    position: absolute;
    background-color: hsl(var(--black));
    padding-block: 3vw;
    padding-inline: 3vw;
    color: white;
    font-size: max(1ch, 1.3vw);
    border-radius: 88px;
    cursor: pointer;
    border: 2px dotted hsl(var(--black));
    margin-bottom: 80px;
    transform: translate3d(0, 0, 0);
    transition: all 0.2s ease;

    right: 1%;
    bottom: calc(1% - 3vw);
}
.fancyButton:hover {
    transform: translate(0px, -4px);
    box-shadow: 1px 15px 30px -12px rgba(255, 255, 255, 0.2);
}
.fancyButton:focus, .fancyButton:focus:hover {
    margin-inline: -5px;
    margin-top: calc(70vh - 10vh) + 5px;
    margin-top: calc(70vh - 10vh);
    margin-left: calc(clamp(320px, 50vw, 100%) - 10vw);
}

.yes, .continue {
    position: absolute;
    text-align: center;
    padding: 16px 29px 16px 29px;
    background-color: hsl(var(--green));
    border: 2px solid hsl(var(--green));
}
.yes:hover, .continue:hover {
    background-color: hsl(var(--green));
    color: #ffffff;
}
.yes:focus, .continue:focus, .yes:focus:hover, .continue:focus:hover {
    border: 5px solid hsl(var(--green-h), var(--green-s), calc(var(--green-l)*0.8));
}
.yes:active:hover, .continue:active:hover {
    color: hsl(var(--green));
}

.no, .reset {
    background-color: hsl(var(--red));
    border: 2px solid hsl(var(--red));
    position: absolute;
    text-align: center;
    padding: 16px 24px 16px 24px;
    /*padding-bottom: 16px;*/
    /*padding-right: 16px;*/
}
.no:hover, .reset:hover {
    background-color: hsl(var(--red));
    color: #ffffff;
}
.no:focus, .reset:focus, .no:focus:hover, .reset:focus:hover {
    border: 5px solid hsl(var(--red-h), var(--red-s), calc(var(--red-l)*0.8));
}
.no:active, .reset:active, .no:active:hover, .reset:active:hover {
    color: hsl(var(--red));
}

.question {
    width: 100%;
    height: 2.5ch;
    padding-top: 0.1ch;
    position: center;
}

.App {
    display: grid;
    grid-template-rows: auto auto 1fr;
    font-family: verdana, arial;
    scroll-behavior: smooth;
    cursor: default;
}


.topbar {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: stretch;
    margin-block: -3vw;
    padding-block: -3vw;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@media (prefers-reduced-motion: no-preference) {
    .navbar {
        opacity: 0;
        animation: fadeInAnimation ease 1s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        animation-delay: 0.7s;
    }

    .fadeIn {
        opacity: 0;
        animation: fadeInAnimation ease 1s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        animation-delay: 1.4s;
        margin: 0 0;
    }
}

