.cards {
    position: relative;
    /*sets the container as a grid with variable number of columns*/
    /*width: calc(100% - 2em);*/
    max-width: 95%;
    display: grid;
    grid-template-columns: auto auto auto;
    grid-gap: 5em;
    margin-inline: auto;
    justify-items: center;
    padding-top: 2ch;
}

.card {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: stretch;
    padding: 1em;
    max-width: 95%;
    /*max-width: 20rem;*/
    min-height: 10rem;
    max-height: 60rem;
    border: 2px white;
    border-radius: 10px;

    background: hsl(calc(var(--background-h)*1.1) calc(var(--background-s)*1.1) calc(var(--background-l)*1.1));
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-width: thin; /* firefox only */
    scrollbar-style: rounded; /* firefox only */
}
/* no firefox only \/\/\/*/
.card::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}
.card::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 8px;
}
.card::-webkit-scrollbar-thumb {
    background-color: hsla(var(--background));
    border-radius: 8px;
}
